	module.exports = {"data":{"site":{"siteMetadata":{"title":"Fog Up Some Comics","author":"Austin Lanari"}},"markdownRemark":{"id":"/home/austin/newFusc/FuSC/src/pages/2017-08-23-Is-That-Not-Kosher/index.md absPath of file >>> MarkdownRemark","html":"<p>(<strong>Note:</strong> This is my first time reviewing something so new so soon, so I’m going to give a fair warning about spoilers. This is the only time I’m going to do this. You can assume if I write about something here, I’m just going to dive in.)</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"undefined\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; z-index: -1; display: block; \"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 48.44267726971505%;position: relative; width: 100%; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Condescending Vivienne\"\n        title=\"\"\n        src=\"/static/cfcfbec2e2f02ddee3c10a57d7dc3cc3-f8fb9.jpg\"\n        srcset=\"/static/cfcfbec2e2f02ddee3c10a57d7dc3cc3-e8976.jpg 148w,\n/static/cfcfbec2e2f02ddee3c10a57d7dc3cc3-63df2.jpg 295w,\n/static/cfcfbec2e2f02ddee3c10a57d7dc3cc3-f8fb9.jpg 590w,\n/static/cfcfbec2e2f02ddee3c10a57d7dc3cc3-85e3d.jpg 885w,\n/static/cfcfbec2e2f02ddee3c10a57d7dc3cc3-d1924.jpg 1180w,\n/static/cfcfbec2e2f02ddee3c10a57d7dc3cc3-9452e.jpg 1770w,\n/static/cfcfbec2e2f02ddee3c10a57d7dc3cc3-8108e.jpg 3018w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>“Homophobia” is a wonderful term because of the way in which it evinces the psychology of the entire enterprise of being repulsed by homosexuals. It is a condition, one best understood as an <em>affliction</em>, where the real victim is the hater: one who would fear someone, as “-phobia” suggests, irrationally. We do not have a term like this that we apply often for individual racism and sexism. While perhaps we should, these things come in different flavors, and there truly is a particular shadow that falls on the person who physically cannot sit in proximity of a gay couple. There is an immediacy to exactly how petty and pathetic this person is, and rarely do people render their discomforts so…</p>\n<p>So <em>performatively</em>.</p>\n<p>Enter <em>The Tar Pit</em>, Jeremy Sorese’s tale (commissioned by Zainab Akhtar’s excellent and inspirational ShortBox project) of a young actress playing beard to two very famous, very in love, very closeted gay actors. The reader enters at a point in which the actress, Vivienne, appears to have tired of Burt and Fred’s lifestyle, which is often punctuated by intense bickering. We quickly find out, however, that there is more to Vivienne’s trepidations.</p>\n<p><em>The Tar Pit</em> immediately grabs the reader with its size: a perfect bound A4 (that’s ~8x11) book sticks out in any pile of mini comics, especially with front and back covers that are this stylish.  If you imagine the following prelude page in full size, you get a pretty good idea of the first impression this book makes.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"undefined\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; z-index: -1; display: block; \"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 138.77777777777777%;position: relative; width: 100%; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"The Tar Pit\"\n        title=\"\"\n        src=\"/static/c1cec86cb6151534085e2421a3f6b699-fb8a0.png\"\n        srcset=\"/static/c1cec86cb6151534085e2421a3f6b699-1a291.png 148w,\n/static/c1cec86cb6151534085e2421a3f6b699-2bc4a.png 295w,\n/static/c1cec86cb6151534085e2421a3f6b699-fb8a0.png 590w,\n/static/c1cec86cb6151534085e2421a3f6b699-526de.png 885w,\n/static/c1cec86cb6151534085e2421a3f6b699-ad082.png 900w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>I adore the top panel of this page because of the number of layers that Sorese is unpacking in one spot, and all before we even get to the page with a Copyright on it. Morty, a reporter and (as we find out later) also gay, is the catalyst in altering the lives of Vivienne, Burt, and Fred. He <em>is</em>, in fact, a somebody in his impact on her life, but not just in terms of altering its course: his very existence as a gay male is this sustained affront to an irrational disgust Vivienne has been nurturing within.</p>\n<p>And that’s why I like the choice to shine the light on him here. With the way his head is cut off as he looks up at the taller Vivienne, and with the light lines coming down at that particular angle across panels, Morty practically looks like he’s part of some kind of new Assumption.  This is especially notable because when we see Morty’s full encounter with Vivienne later on, it is in the dead of night as she looks for Bunny, the escaped dog. Those scenes are characterized by a loose charcoal that follows Vivienne and Morty into a diner, where her homophobia is revealed, as laughing gay men in the diner seen through Vivienne’s eyes briefly comprise a nightmarish scene. Shining the light on Morty here in the prelude thus strikes the exact same “we’re not going anywhere, bitch” tone as the book’s final page, albeit much more subtly.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"undefined\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; z-index: -1; display: block; \"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 77.40207566119852%;position: relative; width: 100%; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Homophobia as horror\"\n        title=\"\"\n        src=\"/static/44b779ad04e1eab40556f3c5322ac087-f8fb9.jpg\"\n        srcset=\"/static/44b779ad04e1eab40556f3c5322ac087-e8976.jpg 148w,\n/static/44b779ad04e1eab40556f3c5322ac087-63df2.jpg 295w,\n/static/44b779ad04e1eab40556f3c5322ac087-f8fb9.jpg 590w,\n/static/44b779ad04e1eab40556f3c5322ac087-85e3d.jpg 885w,\n/static/44b779ad04e1eab40556f3c5322ac087-d1924.jpg 1180w,\n/static/44b779ad04e1eab40556f3c5322ac087-9452e.jpg 1770w,\n/static/44b779ad04e1eab40556f3c5322ac087-2a270.jpg 2987w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>The sustained dark tones, even as the two character move indoors, coupled with pacing of the ensuing converation, lead to a satisfying crescendo that makes Vivienne’s phobia palpable. Sorese renders her response to the situation as dramatic, abrupt, and truly frantic in a fashion befitting a fear bordering on disorder. Just one page—one <em>panel</em>, even—prior to hearing that Morty was gay, Vivienne was all smiles.</p>\n<p>The frantic diner sequence is the climax of the story, which begets a steady wind-down featuring heavier prose and striking color work on each of the remaining pages, usually with only a single major panel. </p>\n<p>The prose throughout is punctuated with wry accounts of the roles that actresses get—“Mother of two growing boys, owner of a finicky cat”—and by the end I had some concern for how this may have tipped a key balance in the book itself. While Vivienne is in some ways a tragic character for whom we cannot help but feel some sympathy, the nature of the diner sequence does well to underscore that even if we understand her own personal struggles, and even if we think Fred and Burt were not the best people, Vivienne, Fred, and Burt were all grown-ass adults. If people want to be shitty, you have no obligation to stick around. And, on an important flip-side, there is no real excuse for internalizing any circumstances and projecting them onto millions of human beings as if they only exist to be your own personal horror show.</p>\n<p>These wry comments, right around round three of the bit, tip this balance of understanding in the direction of the sardonic. The sporadic elbow-jab of “actresses have it rough, lol,” while true, is a weird departure from the tone of the book, which is already a little wobbly, because the pacing in the book sort of accordions: you can feel it squeeze together as it reaches the climax, and a slow release towards the end begets a single drawn out note with just a little bend to it.</p>\n<p>Part of this feels like it’s due to <em>The Tar Pit’s</em> dimensions, or at least the way the excessive space is or is not utilized. From the very first page, because of the size of the panels and the freehand prose lettering, page space just… dies. The direct effect on the initial build-up is fine, but overall these gaps often feel like dead air on the radio. When the ultimate panels on pages get left off before major transitions, the typical suspenseful effect is lessened because the reader is so used to spotting these gaps already. </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"undefined\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; z-index: -1; display: block; \"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 154.28348909657322%;position: relative; width: 100%; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"White space in The Tar Pit\"\n        title=\"\"\n        src=\"/static/919b9ca4037bc640072fd5724852934c-f8fb9.jpg\"\n        srcset=\"/static/919b9ca4037bc640072fd5724852934c-e8976.jpg 148w,\n/static/919b9ca4037bc640072fd5724852934c-63df2.jpg 295w,\n/static/919b9ca4037bc640072fd5724852934c-f8fb9.jpg 590w,\n/static/919b9ca4037bc640072fd5724852934c-85e3d.jpg 885w,\n/static/919b9ca4037bc640072fd5724852934c-d1924.jpg 1180w,\n/static/919b9ca4037bc640072fd5724852934c-9452e.jpg 1770w,\n/static/919b9ca4037bc640072fd5724852934c-8cef6.jpg 2568w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Essentially each part of the story except the climax and the book’s final reprise are affected by these gaps, and I find that to be a good approximation of the work as a whole. When <em>The Tar Pit</em> allows itself to gain momentum in its candid depiction of homophobia, it shines, with the rest feeling <em>just</em> oddly placed enough to notice.</p>","fields":{"slug":"/2017-08-23-Is-That-Not-Kosher/"},"frontmatter":{"title":"Is That Not Kosher?","date":"August 24, 2017","indexImage":{"childImageSharp":{"resize":{"src":"/static/c1cec86cb6151534085e2421a3f6b699-655c4.png","originalName":"the_tar_pit.png"}}},"layout":"post","itemTitle":"The Tar Pit","itemAuthor":"Jeremy Sorese","itemType":"Book","itemPublisher":"ShortBox","description":"Both uneven and brave, The Tar Pit is a colorful cross-section of homophobia in fits and starts."}}},"pathContext":{"slug":"/2017-08-23-Is-That-Not-Kosher/"}}

